.navbar {
    height: 80px;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.3rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: var(--color-background-light);
    box-shadow: var(--box-shadow);
}

.navbar-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 80px;
    max-width: 1500px;
}

.navbar-logo {
    color: var(--color-text);
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    /* text-shadow: var(--color-text) 2px 1px 10px; */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    -webkit-justify-content: end;
            justify-content: end;
}

.nav-item {
    height: 80px;
    text-decoration: none;
}

.nav-links {
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}

.nav-links:hover {
    border-right: 4px solid var(--color-highlight-dark);
    color: var(--color-highlight-dark);
    transition: all 0.1s ease-in-out;
}

.fa-bars {
    color: #fff;
}

.menu-icon {
    display: none;
    color: var(--color-highlight-dark);
}

a{
    text-decoration: none;
    color: var(--color-text);
}

@media screen and (max-width: 960px) {

    .nav-menu {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background:var(--color-background);
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        display: block;
        text-align: center;
        padding: 1.5rem;
        width: 100%;
    }

    .nav-links:hover {
        background-color: var(--color-highlight);
        color: #242424;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(10%, 33%);
                transform: translate(10%, 33%);
        font-size: 1.8rem;
    }

    .menu-icon {
        display: -webkit-flex;
        display: flex;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform: translate(-50%, 100%);
                transform: translate(-50%, 100%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .menu-icon p{
        display: inline-block;
        margin-right: .5rem;
        line-height: 1.8rem;
        font-size: .9rem;
    }

    .fa-times {
        display: inline-block;
        color: var(--color-highlight-dark);
    }
    
    .fa-bars {
        display: inline-block;
        color: var(--color-highlight-dark);
    }
}
