:root {
    --box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.22), 0 1px 2px rgba(0, 0, 0, 0.34);
    --color-highlight: #2E8BC0;
    --color-highlight-dark: #145DA0;
    --color-background: #0c7c7c;
    --color-background-transparent: #0c6d8af1;
    --color-background-light: #00A8A8;
    --color-text: #fff;
    --cyan: #B1D4E0;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    color: white;
    font-family: 'Lato', sans-serif;
    background-color: #00A8A8;
    background-color: var(--color-background-light);
}

h1 {
    letter-spacing: 1px;
    margin: 0;
}

h3 {
    border-bottom: 1px solid #bbb;
    padding-bottom: 10px;
    margin: 40px 0 10px;
}

.btn {
    cursor: pointer;
    background-color: #9c88ff;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.22), 0 1px 2px rgba(0, 0, 0, 0.34);
    box-shadow: var(--box-shadow);
    color: #fff;
    border: 0;
    display: block;
    font-size: 16px;
    margin: 10px 0 30px;
    padding: 10px;
    width: 100%;
}

.fa-viruses {
    color: rgb(196, 80, 80);
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
   }
   ::-webkit-scrollbar-thumb {
    background: #00a8a8;
    border-radius: 0px;
   }
   ::-webkit-scrollbar-thumb:hover{
    background: #008f8f;
   }
   ::-webkit-scrollbar-track{
    background: #ffffff;
    border-radius: 0px;
    box-shadow: inset 7px 10px 12px #f0f0f0;
   }
